@import url("https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css");


.App {
  text-align: center;
}
.box {
  height: 100%;
  padding: 10px;
  margin: 20px;
  border-radius: 15px;
  outline: solid #3a62d3;
}
@media print {
  .midc {
    position: relative;
    bottom: 176px;
  }
  .header-top {
    visibility: hidden;
  }
  .caja {
    visibility: hidden;
  }
  .box {
    outline: none;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
